* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: #212121;
  font-size: 20px;
  text-decoration: none;
}

.button-wrapper {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.button-start {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 10px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.button-stop {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 10px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.button-start:hover, .button-start:focus, .button-stop:hover, .button-stop:focus {
  box-shadow: 0 0 1px 1px #000;
}

